class audittaskexport extends DataBase {
  complete() {
    this.changeAll(this.model.condition)
    this.app.menu.refreshmenu()
    this.resize()
    this.model.tableconfig.tablewidth = ES.selctorDoc('.audittaskexport').box().clientWidth - 240
    this.dom.find('.create').on('click', () => {
      this.app.changePage('createaudittask1', {type: 'new'})
    })
    this.loadlist('group')
    this.dom.find('.searchbtn').on('click', () => {
      this.model.condition.forEach((val) => {
        this.model.apiData[val[0].name] = this.getfiltercondition()[val[0].name].toString() == '' ? null : this.getfiltercondition()[val[0].name].toString()
      })
      this.model.apiData.projectIdList = this.model.apiData.projectIdList ? this.model.apiData.projectIdList.split(',').map(Number) : null
      this.model.apiData.taskIdList = this.model.apiData.taskIdList ? this.model.apiData.taskIdList.split(',').map(Number) : null
      this.model.apiData.page = 1;
      this.search(true)
    })
  }

  async search(bool) {
    let data2 = []
    this.tablecont.showloading()
    let res = await this.api.audit_task_search(this.model.apiData)
    if (res.code == 0) {
      if (res.data.list.length > 0) {
        res.data.list.forEach((val) => {
          for (let i in val) {
            val[i] = val[i] == null ? '' : val[i]
          }
          let obj = {
            id: val.id + ',' + val.seriesAuditNum
          }
          switch (val.status) {
            case 1:
              val.status = '待发布';
              break;
            case 2:
              val.status = '进行中';
              break;
            case 3:
              val.status = '已完成';
              break;
            case 4:
              val.status = '已终结';
              break;
          }
          switch (val.auditChoose) {
            case 1:
              val.auditChoose = '一致部分';
              break;
            case 2:
              val.auditChoose = '不一致部分';
              break;
            case 3:
              val.auditChoose = '全集'
              break;
          }
          obj.operation = this.model.listicon.action;
          data2.push(obj)
        })
        this.tablecont.setData(res.data.list, data2)
      } else {
        this.tablecont.noData();
      }
    }
    if (bool) {
      this.tablecont.getTotal(res.data.pages, 2, res.data.total)
    }
    this.initscroll('scrolltable')
  }

  listaction(value) {
    switch (value.classname) {
      case 'exportdata':
        if (value.id.split(',')[1] > 0) {
          let json = {
            taskId: value.id.split(',')[0]
          }
          let url = this.app.domain1 + 'v1/audit/task/statistics/export?param=' + encodeURI(JSON.stringify(json));
          Tool.downfile(url, this.app)
        } else {
          this.app.alert.show({
            title: ' ',
            msg: '无已审核序列，不支持导出。',
            close: true,
            sure: function () {}
          })
        }
        break;
      case 'exportres':
        if (value.id.split(',')[1] > 0) {
          let json = {
            taskId: value.id.split(',')[0]
          }
          let url = this.app.domain1 + '/v1/audit/task/series/result/export?param=' + encodeURIComponent(JSON.stringify(json));
          Tool.downfile(url, this.app)
        } else {
          this.app.alert.show({
            title: ' ',
            msg: '无已审核序列，不支持导出。',
            close: true,
            sure: function () {}
          })
        }
        break;
    }
  }

  getapidata(value) {
    if (value) {
      switch (value.name) {
        case 'projectIdList':
          this.chooseData.moduleobj.projectIdList.loading(true)
          this.audit_project_name_search(value.data.data ? value.data.data.trim() : "");
          break;
        case 'taskIdList':
          this.chooseData.moduleobj.taskIdList.loading(true)
          this.task_like_query(value.data.data ? value.data.data.trim() : "");
          break;
      }
    } else {
      this.chooseData.moduleobj.projectIdList.loading(true)
      this.chooseData.moduleobj.taskIdList.loading(true)
      this.audit_project_name_search('');
      this.task_like_query('');
    }
  }

  async audit_project_name_search(auditProjectName) {
    let json1 = {
      page: 1,
      pageSize: 10,
      auditProjectName
    };
    let {data} = await this.api.audit_project_name_search(json1)
    this.chooseData.moduleobj.projectIdList.loading(false)
    let temparr = data.list.map((item) => {
      return{
        idx:item.auditProjectId,
        val:item.auditProjectName
      }
    })
    this.chooseData.renderHtml(temparr, 'projectIdList')
  }

  async task_like_query(taskNameKey) {
    let json1 = {
      type: 2,
      taskNameKey
    };
    let {data} = await this.api.task_like_query(json1)
    this.chooseData.moduleobj.taskIdList.loading(false)
    let temparr = data.list ? data.list.map((item) => {
      return {
        idx:item.taskId,
        val:item.taskName
      }
    }):[]
    this.chooseData.renderHtml(temparr, 'taskIdList')
  }

  //报错弹窗
  errorshow(msg) {
    this.app.alert.show({
      title: ' ',
      msg: msg,
      close: false
    })
  }

  resize() {
    let ch = ES.selctorDoc(window).box().clientHeight - 100
    let cw = ES.selctorDoc(window).box().clientWidth - 240
    ES.selctorDoc('.audittaskexport').css({'height': ch, 'width': cw});
    ES.selctorDoc('.scrolltable').css({'height': ch - 200});
  }

}

module.exports = audittaskexport;



